import Vue from 'vue'
import VueRouter from "vue-router"
import routerMap from './router'
import filters from "./filters"
import App from './App.vue'

// Vue filter
Object.keys(filters).forEach((k) => {
  Vue.filter(k, filters[k])
})

// Vue router
Vue.use(VueRouter)

var router = new VueRouter({
	root:"/vue-temp",
	history: false,
	hashbang: true
})

routerMap(router);

