import Index from './views/mIndex.vue'
import Detail from './views/mDetail.vue'
import Error404 from './views/404.vue'


export default (router) => {
router.map({
	"/index": {
		name: 'index',
		component: Index,
        auth: true
	},
	"/detail": {
		name: 'detail',
		component: Detail
	},
	"/404": {
		name: '404',
		component: Error404
	}
})
}



