<template>
  <div>
    <ul>
      <li v-for="item in items" v-link="{name:item.path}">{{item.path | trans}}</li>
    </ul>
    <router-view></router-view>
  </div>
</template>

<script>
export default {
  data () {
    return {
      items:[{
        path: 'index'
      },{
        path: 'detail'
      }
      ]
    }
  }
}
</script>

<style>
body {
  font-family: Helvetica, sans-serif;
}
</style>



